@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap');

* {
  margin: 0;
  padding: 0;
}

body {
  background-image: linear-gradient(to right bottom, #3494e6, #7a8ce7, #ab82dc, #d176c8, #ec6ead);
  font-family: 'Roboto Condensed', sans-serif;
  background-attachment: fixed;
}

.search {
  background-color: transparent;
  border: transparent;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.3);
  color: #fff;
  font-size: 2rem;
  width: 50%;
}

.search:focus {
  outline: none;
}

.movie-card {
  overflow: hidden;
  position: relative;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.5);
  color: #fff;
  border-radius: 2%;
  width: 300px;
  height: 450px;
  font-size: 2rem;
}

.movie-card img {
  width: 100%;
  height: 100%;
}

.movie-info {
  position: absolute;
  color: #fff;
  font-style: italic;
  flex-direction: column;
  text-align: center;  
  background-color: rgb(0, 0, 0, 0.6);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  opacity: 0;
}

.movie-info--blur {
  border-radius: 2%;
  backdrop-filter: blur(5px);
}

.movie-card:hover .movie-info {
  opacity: 1;
  transition: 0.2s ease-in-out;
}

.movie-details {
  color: #fff;
}

.list-group-item {
  background: transparent;
  font-size: 1.3rem;
  overflow: hidden;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.1);
}

.wallpaper {
  border-radius: 20px;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
}

.plot {
  background: transparent;
  font-size: 1.3rem;
  border: transparent;
}

.card-footer {
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.2);
}